#loading {
    position: fixed;
    left: 0;
    top: 0;
    width: 0;
    height: 0;
    z-index: 1002;
    display: table!important;
    table-layout: fixed;
    background-color: rgba(0,0,0,0.7);
    overflow: hidden;
    opacity: 0;
    -moz-transition: opacity .5s ease-in-out;
    -webkit-transition: opacity .5s ease-in-out;
    transition: opacity .5s ease-in-out;
    &, * {
        box-sizing: content-box; }
    &.show {
      opacity: 1; }
    .wrap {
        display: table-cell;
        text-align: center;
        vertical-align: middle; }
    .content {
        position: relative;
        top: 50px;
        display: inline-block; }
    .circle {
        -moz-border-bottom-colors: none;
        -moz-border-left-colors: none;
        -moz-border-right-colors: none;
        -moz-border-top-colors: none;
        -ms-animation: spinPulse 1s infinite ease-in-out 0s;
        animation: 1s ease-in-out 0s normal none infinite running spinPulse;
        background-color: rgba(0, 0, 0, 0);
        border-color: rgba(0, 183, 229, 0.9) rgba(0, 0, 0, 0);
        border-image: none;
        border-radius: 100px;
        border-style: solid;
        border-width: 10px;
        box-shadow: 0 0 70px #2187e7, 0 0 70px #2187e7;
        height: 100px;
        margin: 0 auto;
        opacity: 0.9;
        width: 100px; }
    .circle1 {
        -moz-border-bottom-colors: none;
        -moz-border-left-colors: none;
        -moz-border-right-colors: none;
        -moz-border-top-colors: none;
        -ms-animation: spinoffPulse 1s infinite linear 0s;
        animation: 1s linear 0s normal none infinite running spinoffPulse;
        background-color: rgba(0, 0, 0, 0);
        border-color: rgba(0, 183, 229, 0.9) rgba(0, 0, 0, 0);
        border-image: none;
        border-radius: 100px;
        border-style: solid;
        border-width: 10px;
        box-shadow: 0 0 30px #2187e7, 0 0 30px #2187e7;
        height: 60px;
        margin: 0 auto;
        opacity: 0.9;
        position: relative;
        top: -100px;
        width: 60px; } }

@-ms-keyframes spinPulse {
    0% {
        -ms-transform: rotate(160deg);
        opacity: 0;
        box-shadow: 0 0 1px #2187e7;
 }        //можно попробовать 2 задать
    50% {
        -ms-transform: rotate(145deg);
        opacity: 1; }
    100% {
        -ms-transform: rotate(-320deg);
        opacity: 0; } }

@keyframes spinPulse {
    0% {
        transform: rotate(160deg);
        opacity: 0;
        box-shadow: 0 0 1px #2187e7;
 }        //можно попробовать 2 задать
    50% {
        transform: rotate(145deg);
        opacity: 1; }
    100% {
        transform: rotate(-320deg);
        opacity: 0; } }

@-ms-keyframes spinoffPulse {
    0% {
        -ms-transform: rotate(0deg); }
    100% {
        -ms-transform: rotate(360deg); } }

@keyframes spinoffPulse {
    0% {
        transform: rotate(0deg); }
    100% {
        transform: rotate(360deg); } }

